          addTickets=async(values,setSubmitting)=>{
            const responseData= await addTickets(values);
            console.log(responseData,'response')
            if(responseData.data.message)
            {
                this.setState({ visible:true,massege:'Ticket Add Failed '+responseData.message,msgType:'danger'})
                setSubmitting(false);
            }
            else{
                this.setState({ visible:true,msgType:'success',massege:'Ticket Add Successfully'})
                setSubmitting(false);
                this.props.history.push('/tickets')
                return;
            }
        }

        componentDidMount=async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSets:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 

            const responseDataBed= await getBedSet();
            console.log(responseDataBed,'response')
            if(responseDataBed.status == 200){
                this.setState({bedSets:responseDataBed.data.result})
            } else if (responseDataBed.status == 404) {
                console.warn(responseDataBed.data.message);
            } 
            const responseDataRoom= await getRoomSet();
            console.log(responseDataRoom,'response')
            if(responseDataRoom.status == 200){
                this.setState({roomSets:responseDataRoom.data.result})
            } else if (responseDataRoom.status == 404) {
                console.warn(responseDataRoom.data.message);
            } 
            const responseDataPatient= await getPatientSet();
            console.log(responseDataPatient,'response')
            if(responseDataPatient.status == 200){
                this.setState({patientSets:responseDataPatient.data})
            } else if (responseDataPatient.status == 404) {
                console.warn(responseDataPatient.data.message);
            } 
        }

    
